<template>
    <div class="items-list">
        <div class="filters">
            <Field
                icon="search"
                :placeholder="t('blocks.artists.search')"
                class="noerror light"
                @change="onSearchChange"
            />

            <Filters
                v-if="props.filters?.length"
                :filters="props.filters"
                @change="setFilters"
            />

            <Sort
                v-if="props.sort?.length"
                :sort="props.sort"
                :code="data.sort?.code"
                @change="setSort" />
        </div>

        <ul class="items" v-if="data.items.length">
            <li v-for="(item, id) in data.items"
                :class="{ active: props.id === item.id }"
                @click="emit('change', item)"
            >
                <ArtistPreview
                    :item="item"
                    :row="true"
                    :showState="true"
                />
            </li>
        </ul>

        <Button :loading="data.loading"
            class="tertiary size-l"
            :name="t(`blocks.artists.load`)"
            v-if="data.loading || (data.items.length < data.total)"
            @click="getItems"
        />

        <EmptyLabel
            v-if="!data.items.length && !data.loading"
            class="empty"
            :title="t(`pages.artists.empty.title`)"
            :description="t(`pages.artists.empty.description`)"
        />
    </div>
</template>

<script setup>
    import { useI18n } from 'vue-i18n';
    import { onMounted, reactive, computed } from 'vue';
    import api from '🔗/api';
    import Sort from '🔗/components/ui/Sort.vue';
    import Filters from '🔗/components/ui/Filters.vue';
    import Button from '🔗/components/ui/Button.vue';
    import EmptyLabel from '🔗/components/ui/EmptyLabel.vue';
    import Field from '🔗/components/ui/Field.vue';
    import ArtistPreview from '🔗/components/ui/ArtistPreview.vue';


    const emit = defineEmits(['change']);

    const { t } = useI18n();

    const props = defineProps({
        sort: { type: Array, default: () => ([]) },
        filters: { type: Array, default: () => ([]) },
        endpoint: { type: String, default: 'all' },
        id: { type: [String, null], default: null }
    });

    const data = reactive({
        items: [],
        loading: false,
        skip: 0,
        take: 30, // Temp
        total: 0,
        sort: null,
        searchStr: null,
        filters: {}
    });

    const setFilters = (payload) => {
        data.filters = payload;
        reload();
    };

    const setSort = (item) => {
        if (data.sort?.code === item.code) {
            data.sort = null;
        } else {
            data.sort = item;
        }

        reload();
    };

    const onSearchChange = async (e) => {
        data.searchStr = e.target.value?.toLowerCase().trim() || '';
        data.skip = 0;

        clearTimeout(data.searchTimeout);

        data.searchTimeout = setTimeout(() => {
            delete data.searchTimeout;
            reload();
        }, 200);
    }

    const getItems = async () => {
        data.loading = true;

        if (!data.skip) {
            data.items = [];
        }

        let params = {
            skip: data.skip,
            take: data.take
        };

        if (data.sort) {
            params.sort = data.sort.code;
            params.sort_dir = data.sort.direction;
        }

        if (!!data.searchStr?.trim().length) {
            params.name = data.searchStr.toLowerCase().trim();
        }

        if (data.filters && Object.keys(data.filters)?.length) {
            params = { ...data.filters, ...params };
        }


        let payload;

        if (props.endpoint === 'all') {
            payload = await api.artists().list(params) || [];
        } else if (props.endpoint === 'my-artists') {
            payload = await api.user().getMyArtists(params) || [];
        }

        setTimeout(() => {
            if (payload?.items?.length) {
                data.items.splice(data.items.length, 0, ...(payload.items || []));
            }

            data.skip = data.items.length;
            data.total = payload?.total || 0;

            data.loading = false;
        }, 200);
    };

    const reload = () => {
        data.skip = 0;
        getItems();
    };

    const add = (payload) => {
        data.items.unshift(payload);
        data.total += 1;
    }

    const update = (payload) => {
        if (!payload?.id) {
            return;
        }

        const item = data.items.find(item => item.id === payload.id);
        if (item) {
            data.items.splice(data.items.indexOf(item), 1, payload);
        }
    }

    const remove = (id) => {
        if (!id) {
            return;
        }

        const item = data.items.find(item => item.id === id);
        if (item) {
            data.items.splice(data.items.indexOf(item), 1);
            data.skip -= 1;
            data.total -= 1;
        }
    }

    defineExpose({
        remove,
        add,
        update,
        reload
    });

    onMounted(() => {
        getItems();
    });
</script>

<style lang="scss" scoped>
    @include studio.default-items-list;

    .items-list {
        .items {
            li {
                padding: 4px 8px;
    //             .artist-preview {
    //                 @include artist-list-preview;
    //                 .wathermark,
    //                 img {
    //                     width: 46px;
    //                     height: 46px;
    //                 }
    //             }
            }
        }
    }
</style>