<template>
    <div class="artist-collab-invites" v-if="!data.loading" v-show="!data.updating">
        <label class="title">{{ t(`pages.artists.collab-invites.title`) }}</label>

        <div class="head">
            <Menu :items="data.sections"
                :code="data.section"
                @change="setSection"
            />

            <StateKebabMenu
                :to="false"
                :invert="false"
                :state="data.state"
                :states="data.states"
                @change="setState"
            />
        </div>

        <CollabInvitesReleases v-if="data.section === 'releases' && canEditReleasesContributorState"
            :artist="props.item"
            :state="data.state"
            @total="setTotal('releases', $event)"
        />

        <CollabInvitesTracks v-if="data.section === 'tracks' && canEditTracksContributorState"
            :artist="props.item"
            :state="data.state"
            @total="setTotal('tracks', $event)"
        />
    </div>
</template>

<script setup>
    import { reactive, computed, onMounted } from 'vue';
    import Menu from '🔗/components/ui/Menu.vue';
    import { useI18n } from 'vue-i18n';
    import api from '🔗/api';
    import stores from '@/stores';

    import CollabInvitesTracks from '@/components/blocks/CollabInvites/CollabInvitesTracks.vue';
    import CollabInvitesReleases from '@/components/blocks/CollabInvites/CollabInvitesReleases.vue';
    import StateKebabMenu from '🔗/components/ui/StateKebabMenu.vue';

    const emit = defineEmits(['total']);

    const artists = api.artists();

    const { t } = useI18n();

    const props = defineProps({
        item: { type: [Object, null], default: null }
    });

    const data = reactive({
        state: 'PENDING',
        states: ['PENDING', 'CONFIRMED', 'REJECTED'],
        sections: [
            {
                code: 'releases',
                name: 'pages.artists.collab-invites.releases.title',
                total: 0
            },
            {
                code: 'tracks',
                name: 'pages.artists.collab-invites.tracks.title',
                total: 0
            }
        ],
        section: 'releases'
    });

    const canEditTracksContributorState = computed(() => {
        if (!props.item?.id) {
            return false;
        }

        return api.tracks().canEditContributorState({
            artist: props.item
        })
    });

    const canEditReleasesContributorState = computed(() => {
        if (!props.item?.id) {
            return false;
        }

        return api.releases().canEditContributorState({
            artist: props.item
        })
    });

    const setTotal = (code, total) => {
        const item = data.sections.find(item => item.code === code);

        if (!item) {
            return;
        }

        item.total = total || 0;

        emit('total', data.sections.reduce((total, item) => total += item.total, 0));
    }

    const setSection = (section) => {
        data.section = section;
    }

    const setState = (state) => {
        data.state = state;
        getTotal();
    }

    const getTotal = async () => {
        let params = {
            skip: 0,
            take: 1
        };

        if (props.item?.id) {
            params.artistId = props.item?.id;
        }

        if (data.state) {
            params.state = data.state;
        }

        // Releases
        let payload = await api.releases().contributors.list(params) || [];
        setTotal('releases', payload?.total || 0);

        // Tracks
        payload = await api.tracks().contributors.list(params) || [];
        setTotal('tracks', payload?.total || 0);
    }

    onMounted(() => {
        getTotal();
    });
</script>

<style lang="scss" scoped>
    .artist-collab-invites {
        display: flex;
        flex-direction: column;
        gap: 24px;
        flex: 1;

        .title {
            font: var(--font-h3);
            font-weight: bold;
            color: var(--dark-500);
        }

        .head {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
        }
    }
</style>
