<template>
    <div class="artist-menu">
        <ul class="items">
            <li v-for="(item, id) in items"
                :class="{ active: props.code === item.code, disabled: item.disabled }"
                @click="!item.disabled ? emit('change', item.code) : ''"
            >
                <span>{{ t(item.name) }}</span>

                <Total v-if="props.total && props.total[item.code]" :total="props.total[item.code]" />
            </li>
        </ul>
    </div>
</template>

<script setup>
    import { computed } from 'vue';
    import { useI18n } from 'vue-i18n';
    import api from '🔗/api';
    import stores from '@/stores';
    import Button from '🔗/components/ui/Button.vue';
    import Total from '🔗/components/ui/Total.vue';

    const emit = defineEmits(['change']);

    const { t } = useI18n();

    const props = defineProps({
        code: { type: String, default: '' },
        item: { type: [Object, null], default: null },
        total: { type: [Object, null], default: null }
    });

    // Permissions
        const canEditTeam = computed(() => {
            if (!props.item?.id) {
                return false;
            }

            return api.artists().canEditTeam({
                artist: props.item
            })
        });

        const canEditContributorState = computed(() => {
            if (!props.item?.id) {
                return false;
            }

            return api.tracks().canEditContributorState({
                artist: props.item
            }) || api.releases().canEditContributorState({
                artist: props.item
            })
        });

        

    const items = computed(() => ([
        {
            name: 'pages.artists.menu.info',
            code: 'info'
        }, {
            name: 'pages.artists.menu.metadata',
            code: 'metadata',
            disabled: !props.item?.id
        }, {
            name: 'pages.artists.menu.team',
            code: 'team',
            disabled: !props.item?.id || !canEditTeam.value
        }, {
            name: 'pages.artists.menu.tracks',
            code: 'tracks',
            disabled: !props.item?.id
        }, {
            name: 'pages.artists.menu.releases',
            code: 'releases',
            disabled: !props.item?.id
        }, {
            name: 'pages.artists.menu.collab-invites',
            code: 'collab-invites',
            disabled: !props.item?.id || !canEditContributorState.value
        }
    ]));
</script>

<style lang="scss" scoped>
    .artist-menu {
        width: 30%;
        max-width: 320px;
        flex: 1 1 100%;
        display: flex;
        flex-direction: column;
        border-right: solid 1px var(--secondary-5);
        // border-left: solid 1px var(--secondary-5);
        padding-top: 24px;

        .items {
            display: flex;
            margin: 0;
            padding: 76px 0 0;
            list-style: none;
            flex-direction: column;
            li {
                display: flex;
                min-height: 45px;
                align-items: center;
                cursor: pointer;
                padding: 0 8px;
                gap: 8px;
                & > span {
                    font: var(--font-body-1);
                    color: var(--dark-500);
                }


                &.active {
                    background: var(--light-20);
                    & > span {
                        font-weight: bold;
                    }
                }

                &.disabled {
                    cursor: default;
                    background-color: transparent;
                    & > span {
                        color: var(--dark-20); 
                    }
                    &:hover {
                        background-color: transparent;
                        & > span {
                            font-weight: normal;
                        }
                    }
                }

                &:hover {
                    background: var(--light-10);
                    & > span {
                        font-weight: bold;
                    }
                }
            }
        }
    }
</style>