<template>
    <div class="links-field">
        <div
            v-for="(item, index) in fields"
            :key="index"
        >
            <Field
                :icon="item.code && knownSites.get(item.code) ? item.code : 'link'"
                :value="item.value"
                :disabled="props.disabled"
                :placeholder="t('ui.links-field.placeholder')"
                :class="{ 'with-error': isInvalid(index), light: true, noerror: true }"
                @change="onChange($event, index)"
            />

            <IconButton
                icon="minus"
                class="size-m secondary"
                :disabled="props.disabled"
                @click="removeField(index)"
            />
        </div>

        <IconButton
            icon="plus"
            class="size-m"
            :disabled="props.disabled"
            @click="addField"
        />
    </div>
</template>

<script setup>
    import { useI18n } from 'vue-i18n';
    import { reactive, watch, onMounted } from 'vue';
    import Field from '🔗/components/ui/Field.vue';
    import IconButton from '🔗/components/ui/IconButton.vue';
    import { knownSites, detectSiteCode, isValidURL } from '🔗/utils/utils';

    const { t } = useI18n();
    const props = defineProps({
        value: { type: Object, default: () => ({}) },
        disabled: { type: Boolean, default: () => false }
    });

    const emit = defineEmits(['change']);

    const isInvalid = (index) => {
        const val = fields[index].value;
        return val && !isValidURL(val);
    }

    const fields = reactive(
        Object.entries(props.value).map(([code, value]) => ({ code, value }))
    );

    watch(
        () => props.value,
        (newVal) => {
            fields.splice(0, fields.length, ...Object.entries(newVal).map(([code, value]) => ({ code, value })));
        },
        { deep: true }
    );

    const emitValue = () => {
        const result = {};
        for (const item of fields) {
            if (item.code && item.value) {
                result[item.code] = item.value;
            }
        }
        emit('change', result);
    }

    const onChange = (e, index) => {
        const input = e.target.value.trim();
        const siteCode = detectSiteCode(input);

        fields[index].value = input;
        fields[index].code = siteCode;

        emitValue();
    }

    const addField = () => {
        fields.push({ code: '', value: '' });
    }

    const removeField = (index) => {
        fields.splice(index, 1);
        emitValue();
    }

    onMounted(() => {
        if (!fields?.length) {
            addField();
        }
    })
</script>

<style lang="scss" scoped>
.links-field {
    display: flex;
    flex-direction: column;
    gap: 8px;

    button {
        flex-shrink: 0;
    }

    & > div {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 8px;

        .field {
            flex: 1 1;
            max-width: var(--input-max-width);
        }
    }

    & > button {
        align-self: flex-start;
    }
}
</style>
