<template>
    <div class="collab-invites-list">
        <ul>
            <li v-for="item in data.items">
                <TrackPreview :item="item.track"
                />

                <label class="role">
                    <span>
                        {{ t(`pages.artists.collab-invites.role`) }}
                    </span>
                    <span>
                        {{ t(data.rolesByCode[item.role]) }}
                    </span>
                </label>

                <IconState :state="item.state" v-if="item.state" />


                <Button v-if="item.state !== 'REJECTED'"
                    :name="t('pages.artists.collab-invites.reject.title')"
                    class="size-s secondary"
                    :disabled="item.state === 'CONFIRMED' && item.role === 'PRIMARY'"
                    @click="reject(item)"
                />

                <Button v-if="item.state !== 'CONFIRMED'"
                    :name="t('pages.artists.collab-invites.confirm.title')"
                    class="size-s"
                    @click="confirm(item)"
                />
            </li>
        </ul>

        <LoadMore
            :loading="data.loading"
            :values="data.items"
            :total="data.total"
            :take="data.take"
            pluralization="collab"
            @click="getItems"
        />

        <EmptyLabel
            v-if="!data.items.length && !data.loading"
            class="empty"
            :title="t(`pages.artists.collab-invites.tracks.empty.title`)"
            :description="t(`pages.artists.collab-invites.tracks.empty.description`)"
        />
    </div>
</template>

<script setup>
    import { useI18n } from 'vue-i18n';
    import { onMounted, reactive, watch } from 'vue';

    import Button from '🔗/components/ui/Button.vue';
    import IconState from '🔗/components/ui/IconState.vue';
    import LoadMore from '🔗/components/ui/LoadMore.vue';
    import EmptyLabel from '🔗/components/ui/EmptyLabel.vue';
    import TrackPreview from '🔗/components/ui/TrackPreview.vue';
    import { TRACK_CONTRIBUTOR_ROLES } from '🔗/constants';

    import api from '🔗/api';
    import stores from '@/stores';

    const emit = defineEmits(['total']);

    const modals = stores.modals();

    const { t } = useI18n();

    const props = defineProps({
        artist: { type: [Object, null], default: null },
        state: { type: String, default: 'PENDING' },
    });

    const data = reactive({
        items: [],
        loading: false,
        skip: 0,
        take: 30,
        total: 0,
        filters: {},
        rolesByCode: {}
    });

    TRACK_CONTRIBUTOR_ROLES.forEach(item => data.rolesByCode[item.code] = item.name);

    const getItems = async () => {
        data.loading = true;

        if (!data.skip) {
            data.items = [];
        }

        let params = {
            skip: data.skip,
            take: data.take
        };

        if (props.artist?.id) {
            params.artistId = props.artist?.id;
        }

        if (props.state) {
            params.state = props.state;
        }

        const payload = await api.tracks().contributors.list(params) || [];

        setTimeout(() => {
            if (payload?.items?.length) {
                data.items.splice(data.items.length, 0, ...payload.items);
            }

            data.skip = data.items.length;
            data.total = payload?.total || 0;

            // emit('total', data.total);

            data.loading = false;
        }, 200);
    };

    const decreaseTotal = () => {
        data.skip -= 1;
        data.total -= 1;
        emit('total', data.total);
    }

    const reload = () => {
        data.skip = 0;
        getItems();
    };

    const reject = async (item) => {
        modals.confirm({
            title: t(`pages.artists.collab-invites.tracks.reject.confirm.title`),
            body: t(`pages.artists.collab-invites.tracks.reject.confirm.description`, {
                title: item.track.title,
                role: t(data.rolesByCode[item.role])
            }),
            onOk: async () => {
                await api.tracks().contributors.changeState(item.id, 'REJECTED');
                
                item.state = 'REJECTED';

                decreaseTotal();
                
                modals.open('reject', {
                    data: {
                        title: t(`pages.artists.collab-invites.tracks.reject.done.title`, {
                            title: item.track.title,
                            role: t(data.rolesByCode[item.role])
                        }),
                        close: t(`pages.artists.collab-invites.tracks.reject.done.close`)
                    },
                    quietClose: true
                });
            }
        }); 
    }

    const confirm = async (item) => {
        modals.confirm({
            title: t(`pages.artists.collab-invites.tracks.confirm.confirm.title`),
            body: t(`pages.artists.collab-invites.tracks.confirm.confirm.description`, {
                title: item.track.title,
                role: t(data.rolesByCode[item.role])
            }),
            onOk: async () => {
                await api.tracks().contributors.changeState(item.id, 'CONFIRMED');
                
                item.state = 'CONFIRMED';

                decreaseTotal();

                modals.open('success', {
                    data: {
                        title: t(`pages.artists.collab-invites.tracks.confirm.done.title`, {
                            title: item.track.title,
                            role: t(data.rolesByCode[item.role])
                        }),
                        close: t(`pages.artists.collab-invites.tracks.confirm.done.close`)
                    },
                    quietClose: true
                });
            }
        }); 
    }

    watch(
        () => props.state,
        () => {
            reload();
        }
    );

    onMounted(() => {
        getItems();
    });
</script>

<style lang="scss" scoped>
    @include studio.collab-invites-list;
</style>