<template>
    <PageHeader :title="t(`pages.artists.title`)"
        :detail="!!data.item && data.section !== 'collab-invites'"
        :secondary="!!data.selectedTracks?.length || !!data.selectedReleases?.length"
        id="item-head-buttons"
        v-show="!data.trackEditorMode && !data.releaseEditorMode && !data.licenseTrackEditorMode"
    >
        <template #title v-if="data.item">
            <IconButton
                icon="arrow-left"
                @click="close"
                class="size-l tertiary rtl-sensitive"
                :name="t(`pages.artists.cancel`)"
            />

            <PageHeaderArtistTitle :artist="data.item" />
        </template>

        <template #default>
            <Button
                v-if="!data.item && canAdd"
                icon="plus"
                class="size-s"
                :name="t(`pages.artists.add.title`)"
                @click="add"
            />

            <template v-if="data.item && ['info', 'metadata', 'team'].indexOf(data.section) !== -1">
                <StateKebabMenu v-if="data.item?.id && canEdit"
                    :state="data.item?.state"
                    :states="data.states"
                    @change="changeState"
                />

                <KebabMenu
                    v-if="data.item?.id"
                    :leftAuto="!locale.rtl"
                    :rightAuto="locale.rtl"
                    :tertiary="true"
                    :invert="true"
                    size="size-s"
                >
                    <ul>
                        <li @click="remove" v-if="canRemove">
                            <Icon icon="trash" />
                            <span>{{ t(`pages.artists.remove.title`) }}</span>
                        </li>

                        <li @click="close">
                            <Icon icon="close" />
                            <span>{{ t(`pages.artists.cancel`) }}</span>
                        </li>
                    </ul>
                </KebabMenu>
            </template>
        </template>
    </PageHeader>

    <div class="box" v-show="!data.trackEditorMode && !data.releaseEditorMode && !data.licenseTrackEditorMode">
        <Artists
            v-show="!data.item"
            :id="data.item?.id"
            :sort="ARTISTS_SORT"
            :filters="ARTISTS_FILTERS"
            :endpoint="user.isArtistCurator ? 'all' : 'my-artists' "
            ref="artists"

            @change="onSelectItem" />

        <ArtistMenu 
            v-if="data.item"
            :code="data.section"
            :item="data.item"
            :total="data.total"
            @change="setSection"
        />

        <div v-if="data.item" class="item-detail">
            <!-- Info -->
            <ArtistInfo
                v-if="data.section === 'info'"
                :item="data.item"
                @add="onAdd"
                @update="onUpdate"
                @edit="onEdit"
            />

            <!-- Metadata -->
            <ArtistMetadata
                v-if="data.section === 'metadata'"
                :item="data.item"
                @update="onUpdate"
                @edit="onEdit"
            />

            <!-- Team -->
            <ArtistTeam
                v-if="data.section === 'team' && canEditTeam"
                :item="data.item"
                @update="onUpdate"
                @edit="onEdit"
            />

            <!-- Tracks -->
            <Tracks
                v-if="data.section === 'tracks'"
                :artist="data.item"
                @select="onSelectTracks"
                @add="addTracks"
                @edit="editTracks"
                @licenses="editTracksLicenses"
                ref="tracks"
            />

            <!-- Releases -->
            <Releases
                v-if="data.section === 'releases'"
                :artist="data.item"
                @select="onSelectReleases"
                @add="addReleases"
                @edit="editReleases"
                @editTracks="editReleaseTracks"
                @editTracksLicenses="editReleaseTracksLicenses"
                ref="releases"
            />

            <!-- Collab Invites -->
            <ArtistCollabInvites
                v-if="data.section === 'collab-invites' && canEditContributorState"
                :item="data.item"
                @total="data.total['collab-invites'] = $event;"
            />
        </div>
    </div>

    <!-- Редактирование релизов -->
    <ReleaseEditor v-if="data.releaseEditorMode"
        :artist="data.item"
        :items="data.editorReleases"
        ref="releaseEditor"
        @remove="onRemoveRelease"
        @update="onUpdateRelease"
        @add="onAddRelease"
        @close="closeReleaseEditorMode"
    />

    <!-- Редактирование треков -->
    <TrackEditor v-if="data.trackEditorMode"
        :artist="data.item"
        :release="data.editorReleases?.length ? data.editorReleases[0] : null"
        :items="data.editorTracks"
        ref="trackEditor"
        @remove="onRemoveTrack"
        @update="onUpdateTrack"
        @add="onAddTrack"
        @close="closeTrackEditorMode"
    />

    <!-- Редактирование лицензий треков -->
    <LicenseTrackEditor v-if="data.licenseTrackEditorMode"
        :artist="data.item"
        :release="data.editorReleases?.length ? data.editorReleases[0] : null"
        :items="data.editorTracks"
        ref="licenseTrackEditor"
        @close="closeLicenseTrackEditorMode"
    />
</template>

<script setup>
    import { reactive, computed, ref } from 'vue';
    import Img from '🔗/components/ui/Img.vue';
    import Button from '🔗/components/ui/Button.vue';
    import IconButton from '🔗/components/ui/IconButton.vue';
    import PageHeader from '@/components/blocks/PageHeader/PageHeader.vue';
    import PageHeaderArtistTitle from '@/components/blocks/PageHeader/PageHeaderArtistTitle.vue';

    import Artists from './components/Artists.vue';
    import ArtistMenu from './components/ArtistMenu.vue';
    import ArtistInfo from './components/ArtistInfo.vue';
    import ArtistMetadata from './components/ArtistMetadata.vue';
    import ArtistTeam from './components/ArtistTeam.vue';
    import ArtistCollabInvites from './components/ArtistCollabInvites.vue';

    import KebabMenu from '🔗/components/ui/KebabMenu.vue';
    import StateKebabMenu from '🔗/components/ui/StateKebabMenu.vue';

    import Tracks from '@/components/blocks/Tracks.vue';
    import TrackEditor from '@/components/blocks/TrackEditor/TrackEditor.vue';
    import LicenseTrackEditor from '@/components/blocks/LicenseTrackEditor/LicenseTrackEditor.vue';

    import Releases from '@/components/blocks/Releases.vue';
    import ReleaseEditor from '@/components/blocks/ReleaseEditor/ReleaseEditor.vue';

    import { ARTISTS_SORT, ARTISTS_FILTERS } from '🔗/constants';

    import { useI18n } from 'vue-i18n';

    import api from '🔗/api';
    import stores from '@/stores';
    const user = stores.user();
    const modals = stores.modals();
    const locale = stores.locale();

    const { t } = useI18n();

    const releases = ref(null)
    const tracks = ref(null)
    const artists = ref(null)
    const trackEditor = ref(null);
    const licenseTrackEditor = ref(null);
    const releaseEditor = ref(null);

    const data = reactive({
        states: [],
        section: 'info',
        stateOpened: false,
        item: null,
        trackEditorMode: false,
        licenseTrackEditorMode: false,
        editorTracks: null,
        selectedTracks: null,
        releaseEditorMode: false,
        editorReleases: null,
        selectedReleases: null,
        total: {
            'collab-invites': 0    
        }
    });

    // Permitions
        const canAdd = computed(() => {
            return api.artists().canAdd({
                artist: { id: data.item }
            })
        });

        const canEdit = computed(() => {
            if (!data.item?.id) {
                return false;
            }

            return api.artists().canEdit({
                artist: data.item
            })
        });

        const canEditTeam = computed(() => {
            if (!data.item?.id) {
                return false;
            }

            return api.artists().canEditTeam({
                artist: data.item
            })
        });

        const canRemove = computed(() => {
            if (!data.item) {
                return false;
            }

            return api.artists().canRemove({
                artist: { id: data.item }
            })
        });

        const canEditContributorState = computed(() => {
            if (!data.item?.id) {
                return false;
            }

            return api.tracks().canEditContributorState({
                artist: data.item
            }) || api.releases().canEditContributorState({
                artist: data.item
            })
        });
        

    const setSection = (section) => {
        checkUnsaved(() => {
            data.editorReleases = null;
            data.editorTracks = null;
            data.selectedTracks = null;
            data.selectedReleases = null;

            data.section = section;
        });
    }

    const close = () => {
        checkUnsaved(() => {
            data.item = null;
            data.section = 'info';
            data.states = [];
            data.editorReleases = null;
            data.editorTracks = null;
            data.selectedTracks = null;
            data.selectedReleases = null;
        });
    }

    const onSelectItem = (item) => {
        checkUnsaved(() => {
            close();

            setTimeout(() => {
                data.item = item?.id ? item : null;
                getStates();
                getTotal();
            });
        });
    }

    // Total
        const getTotal = async () => {
            data.total['collab-invites'] = 0;

            let params = {
                skip: 0,
                take: 1
            };

            if (data.item?.id) {
                params.artistId = data.item?.id;
            }

            params.state = 'PENDING';

            // Tracks
            let payload = await api.tracks().contributors.list(params) || [];
            data.total['collab-invites'] += payload?.total || 0;

            payload = await api.releases().contributors.list(params) || [];
            data.total['collab-invites'] += payload?.total || 0;
        }

    // Artist
        const add = () => {
            checkUnsaved(() => {
                close();

                setTimeout(() => {
                    data.item = {};
                });
            });
        }

        const getStates = async () => {
            data.states = await api.artists().getAvailableStates(data.item?.id) || [];
        }

        const changeState = async (state) => {
            modals.confirm({
                title: t(`pages.artists.state.confirm.title`),
                body: t(`pages.artists.state.confirm.description`, {
                    name: t(`state.default.code.${ state.toLowerCase() }`)
                }),
                onOk: async () => {
                    const payload = await api.artists().changeState(data.item?.id, state);

                    data.item.state = payload?.state;
                    getStates();

                    modals.open('success', {
                        data: {
                            title: t(`pages.artists.state.done.title`),
                            close: t(`pages.artists.state.done.close`)
                        },
                        quietClose: true
                    });
                }
            }); 
        }

        const remove = async () => {
            if (!data.item?.id) {
                return;
            }

            modals.confirm({
                title: t(`pages.artists.remove.confirm.title`),
                body: t(`pages.artists.remove.confirm.description`, { name: data.item?.name }),
                onOk: async () => {
                    await api.artists().remove(data.item?.id);

                    modals.open('success', {
                        data: {
                            title: t(`pages.artists.remove.done.title`),
                            close: t(`pages.artists.remove.done.close`)
                        },
                        quietClose: true
                    });
                    artists.value.remove(data.item?.id);

                    delete data.hasChanges;

                    close();
                }
            }); 
        }

        const checkUnsaved = async (callback) => {
            if (data.hasChanges) {
                modals.confirm({
                    title: t(`pages.artists.unsaved.confirm.title`),
                    body: t(`pages.artists.unsaved.confirm.description`),
                    onOk: async() => {
                        delete data.hasChanges;

                        callback();
                    }
                }); 
                return;
            }

            callback();
        }

        const onUpdate = (payload) => {
            artists.value.update(payload)
            data.item = payload;

            delete data.hasChanges;
        }

        const onEdit = () => {
            data.hasChanges = true;
        }

        const onAdd = (payload) => {
            artists.value.add(payload)
            data.item = payload;

            delete data.hasChanges;
        }

    // Tracks
        const onSelectTracks = (tracks) => {
            data.selectedTracks = tracks?.length ? tracks : null;
        }

        const addTracks = (count) => {
            data.trackEditorMode = true;

            // 
            setTimeout(() => {
                trackEditor.value?.create(count || 1);
            });
        }

        const editTracks = (tracks) => {
            data.editorTracks = tracks;
            data.trackEditorMode = true;
        }

        const closeTrackEditorMode = () => {
            data.trackEditorMode = false;
        }
            // Events
            const onAddTrack = (track) => {
                if (track?.id && tracks?.value) {
                    tracks.value.addItem(track);
                }
            }

            const onRemoveTrack = (id) => {
                if (id && tracks?.value) {
                    tracks.value.removeItems([{ id }], true)
                }
            }

            const onUpdateTrack = (track) => {
                if (track?.id && tracks?.value) {
                    tracks.value.updateItem(track);
                }
            }

        const editTracksLicenses = (tracks) => {
            data.editorTracks = tracks;
            data.licenseTrackEditorMode = true;
        }

        const closeLicenseTrackEditorMode = () => {
            data.licenseTrackEditorMode = false;
        }

    // Releases
        const onSelectReleases = (releases) => {
            data.selectedReleases = releases?.length ? releases : null;
        }

        const addReleases = (count) => {
            data.releaseEditorMode = true;

            // 
            setTimeout(() => {
                releaseEditor.value?.create(count || 1);
            });
        }

        const editReleases = (releases) => {
            data.editorReleases = releases;
            data.releaseEditorMode = true;
        }

        const editReleaseTracks = async (release) => {
            const payload = await api.releases().tracks.list(release.id);

            data.editorTracks = payload?.items || [];
            data.editorReleases = [...(data.selectedReleases || [])];
            data.trackEditorMode = true;
        }

        const editReleaseTracksLicenses = async (release) => {
            const payload = await api.releases().tracks.list(release.id);

            data.editorTracks = payload?.items || [];
            data.editorReleases = [...(data.selectedReleases || [])];
            data.licenseTrackEditorMode = true;
        }

        const closeReleaseEditorMode = () => {
            data.releaseEditorMode = false;
        }
            // Events
            const onAddRelease = (release) => {
                if (release?.id) {
                    releases.value.addItem(release);
                }
            }

            const onRemoveRelease = (id) => {
                if (id) {
                    releases.value.removeItems([{ id }], true)
                }
            }

            const onUpdateRelease = (release) => {
                if (release?.id) {
                    releases.value.updateItem(release);
                }
            }
</script>

<style lang="scss" scoped>
    @include studio.default-box;

    .chevron-down {
        fill: var(--white-500);
        stroke: none;
        transition: all .4s;

        &.opened {
            transform: rotate(180deg);
        }
    }
</style>
