<template>
    <div class="artist-info" v-if="!data.loading" v-show="!data.updating">
        <label class="title">{{ t(`pages.artists.${ props.item?.id ? 'edit' : 'add' }.title`) }}</label>

        <div class="form-line">
            <label>{{ t(`pages.artists.fields.name`) }}</label>

            <Field
                :placeholder="t(`pages.artists.fields.name`)"
                :value="data.name"
                :disabled="!canEditByCurator"
                class="noerror light with-placeholder"
                @change="onNameChange"
            />
        </div>

        <div class="form-line">
            <label>{{ t(`pages.artists.fields.code.name`) }}</label>

            <div class="box">
                <Field
                    :placeholder="t(`pages.artists.fields.code.name`)"
                    :value="data.code"
                    :disabled="!canEditByCurator"
                    :maxlength="20"
                    :mask="maskArtistCode"
                    class="noerror light with-placeholder"
                    @change="onCodeChange"/>

                <span class="attention">
                    <span>{{ t(`pages.artists.fields.code.subattention1`) }}</span>
                    <span>{{ t(`pages.artists.fields.code.subattention2`) }}</span>
                </span>
            </div>
        </div>

        <div class="form-line">
            <label>
                <span>
                    {{ t(`pages.artists.fields.profile-image`) }}
                </span>
                <span class="attention">
                    {{ t(`pages.artists.fields.min-profile-image-size`) }}
                </span>
            </label>

            <div class="profile-image-preview">
                <FileUploader 
                    class="upload"
                    icon="upload-img"
                    :acceptedFormats="['image/png', 'image/jpeg', 'image/jpg', 'image/gif', 'image/jfif']"
                    :loading="data.loading"
                    :dropZone="true"
                    :showPreview="true"
                    :preview="data.profileImage?.resized"
                    :zoneSize="{ width: '120px', height: '120px' }"
                    zoneType="square-image"
                    :disabled="!canEdit"
                    @change="updateProfileImage"
                />
            </div>
        </div>

        <div class="form-line">
            <label>{{ t(`pages.artists.fields.description`) }}</label>

            <Textarea
                :placeholder="t(`pages.artists.fields.description`)"
                :value="data.description"
                class="noerror light with-placeholder"
                :disabled="!canEdit"
                @change="onDescriptionChange"
            />
        </div>

        <div class="form-line">
            <label>{{ t(`pages.artists.fields.links`) }}</label>

            <LinksField
                :placeholder="t(`pages.artists.fields.links`)"
                :disabled="!canEdit"
                :value="data.links"
                @change="onLinksChange"
            />
        </div>

        <div class="form-line">
            <label>
                <span>
                    {{ t(`pages.artists.fields.email`) }}
                </span>
                <span class="attention">
                    {{ t(`pages.artists.fields.email-goal`) }}
                </span>
            </label>

            <Field
                :placeholder="t(`pages.artists.fields.email`)"
                :disabled="!canEdit"
                :value="data.email"
                type="email"
                class="noerror light with-placeholder"
                :class="{ 'with-error': isInvalidEmail(data.email) }"
                @change="onEmailChange"
            />
        </div>

        <Teleport to="#item-head-buttons">
            <Button :name="t(`pages.artists.${ props.item?.id ? 'edit' : 'add' }.save`)"
                class="size-s secondary"
                :disabled="saveDisabled"
                v-if="canEdit"
                @click="saveChanges"
            />
        </Teleport>
    </div>
    <div class="loader" v-if="data.loading || data.updating">
        <div>
            <Loader :inside="true" class="tertiary" />
        </div>
    </div>
</template>

<script setup>
    import { reactive, computed, onMounted } from 'vue';
    import Button from '🔗/components/ui/Button.vue';
    import Field from '🔗/components/ui/Field.vue';
    import Textarea from '🔗/components/ui/Textarea.vue';
    import LinksField from '🔗/components/ui/LinksField.vue';
    import { useI18n } from 'vue-i18n';
    import api from '🔗/api';
    import FileUploader from '🔗/components/ui/FileUploader.vue';
    import Loader from '🔗/components/ui/Loader.vue';
    import { delay, message, isInvalidEmail } from '🔗/utils/utils';
    import stores from '@/stores';
    import { maskArtistCode } from '🔗/utils/utils';
    // const modals = stores.modals();
    const artists = api.artists();
    const user = stores.user();

    const emit = defineEmits(['update', 'add', 'edit']);
    const { t } = useI18n();

    const props = defineProps({
        item: { type: [Object, null], default: null }
    });

    const data = reactive({
        id: null,
        code: null,
        name: null,
        email: null,
        description: null,
        profileImage: {},
        links: {},
        newProfileImage: null,
        loading: true
    });

    const saveDisabled = computed(() => {
        return !data.name || !data.code || !/^[a-z0-9_]{3,20}$/.test(data.code) || data.code?.length < 3 || (data.email && isInvalidEmail(data.email)) || !data.hasChanges;
    });

    const canEdit = computed(() => {
        if (data.id) {
            return api.artists().canEdit({
                artist: { id: data.id }
            });
        }

        return api.artists().canAdd();
    });

    const canEditByCurator = computed(() => {
        if (data.id) {
            return api.artists().canEditByCurator({
                artist: { id: data.id }
            });
        }

        return api.artists().canAdd();
    });

    const onCodeChange = (e) => {
        if (data.code !== e.target.value?.trim()) {
            data.code = e.target.value?.trim();
            onEdit();
        }
    }

    const onNameChange = (e) => {
        data.name = e.target.value?.trim();
        onEdit();
    }

    const onEmailChange = (e) => {
        data.email = e.target.value?.trim();
        onEdit();
    }

    const onDescriptionChange = (e) => {
        data.description = e.target.value?.trim();
        onEdit();
    }

    const onLinksChange = (links) => {
        data.links = links || {};
        onEdit();
    }

    const updateProfileImage = (e) => {
        if (e?.name) {
            data.newProfileImage = e;
            onEdit();
        }
    }

    const onEdit = () => {
        emit('edit');
        data.hasChanges = true;
    }

    const saveChanges = async () => {
        const artistInfo = {
            name: data.name,
            code: data.code,
            description: data.description || null,
            links: Object.keys(data.links || {}).map(code => ({ code, url: data.links[code] })),
            email: data.email || null
        }

        // Если представитель артиста, то убираем name

        data.updating = true;

        let payload;

        if (data.id) {
            payload = await artists.update(data.id, artistInfo);
        } else {
            payload = await artists.add(artistInfo);
        }

        if (payload?.id && data.newProfileImage) {
            payload = await artists.updateProfileImage(payload.id, data.newProfileImage);
        }

        await delay(200);
        data.updating = false;

        if (payload?.id) {
            message.info(t(`pages.artists.${ data.id ? 'edit' : 'add' }.done.title`));
            // modals.open('success', {
            //     data: {
            //         timer: 3,
            //         title: t(`pages.artists.${ data.id ? 'edit' : 'add' }.done.title`),
            //         close: t(`pages.artists.${ data.id ? 'edit' : 'add' }.done.close`)
            //     },
            //     quietClose: true
            // });
        }

        if (data.id) {
            emit('update', payload);
        } else {
            emit('add', payload);
        }

        data.code = payload.code;
        data.id = payload.id;
        data.profileImage = payload.profileImage || {};
        data.newProfileImage = null;

        delete data.hasChanges;
    }

    const getItem = async () => {
        data.loading = true;

        const payload = await artists.get(props.item.id);

        data.id = payload?.id;
        data.code = payload?.code;
        data.name = payload?.name;
        data.email = payload?.email;
        data.description = payload?.description;

        data.profileImage = payload?.profileImage || {};

        const links = {};
        payload?.links?.forEach(item => links[item.code] = item.url);
        data.links = links || {};

        await delay(200);

        data.loading = false;

        delete data.hasChanges;
    }

    onMounted(() => {
        if (props.item?.id) {
            getItem();
        } else {
            data.loading = false;
        }
    });
</script>

<style lang="scss" scoped>
    .loader {
        @include inside-loader;
    }

    .artist-info {
        display: flex;
        flex-direction: column;
        gap: 24px;
        .title {
            font: var(--font-h3);
            font-weight: bold;
            color: var(--dark-500);
        }
    }

    .form-line {
        .profile-image-preview {
            position: relative;
            background-size: cover;
            background-position: center;
            border-radius: 10px;
            img {

            }
        }
    }

    // .buttons {
    //     display: flex;
    //     flex-direction: row;
    //     align-items: center;
    //     gap: 8px;
    //     margin-top: 32px;
    // }
</style>
