<template>
    <div class="artist-metadata" v-if="!data.loading" v-show="!data.updating">
        <label class="title">{{ t(`pages.artists.dict.title`) }}</label>

       <div class="form-line">
            <label>{{ t('dicts.instruments') }}</label>

            <DictAutoComplete
                :placeholder="t(`dicts.instruments`)"
                :values="data.instruments"
                :disabled="!canEdit"
                dict="instruments"
                class="noerror light with-placeholder"
                @change="onInstrumentsChange"
            />
        </div>

        <div class="form-line">
            <label>{{ t('dicts.genres') }}</label>

            <DictAutoComplete
                :placeholder="t(`dicts.genres`)"
                :values="data.genres"
                :disabled="!canEdit"
                dict="genres"
                class="noerror light with-placeholder"
                @change="onGenresChange"
            />
        </div>

        <div class="form-line">
            <label>{{ t('dicts.categories') }}</label>

            <DictAutoComplete
                :placeholder="t(`dicts.categories`)"
                :values="data.categories"
                :disabled="!canEdit"
                dict="categories"
                class="noerror light with-placeholder"
                @change="onCategoriesChange"
            />
        </div>

        <div class="form-line">
            <label>{{ t('dicts.moods') }}</label>

            <DictAutoComplete
                :placeholder="t(`dicts.moods`)"
                :values="data.moods"
                :disabled="!canEdit"
                dict="moods"
                class="noerror light with-placeholder"
                @change="onMoodsChange"
            />
        </div>

        <div class="form-line">
            <label>{{ t('dicts.tags') }}</label>

            <DictAutoComplete
                :placeholder="t(`dicts.tags`)"
                :values="data.tags"
                :disabled="!canEdit"
                dict="tags"
                class="noerror light with-placeholder"
                @change="onTagsChange"
            />
        </div>


        <Teleport to="#item-head-buttons">
            <Button :name="t(`pages.artists.edit.save`)"
                :disabled="!canEdit || !data.hasChanges"
                class="size-s secondary"
                @click="saveChanges"
            />
        </Teleport>
    </div>
    <div class="loader" v-if="data.loading || data.updating">
        <div>
            <Loader :inside="true" class="tertiary" />
        </div>
    </div>
</template>

<script setup>
    import { reactive, computed, onMounted } from 'vue';
    import Button from '🔗/components/ui/Button.vue';
    import DictAutoComplete from '🔗/components/ui/DictAutoComplete.vue';
    import { useI18n } from 'vue-i18n';
    import api from '🔗/api';
    import Loader from '🔗/components/ui/Loader.vue';
    import { delay, message } from '🔗/utils/utils';
    import stores from '@/stores';
    const modals = stores.modals();
    const artists = api.artists();
    const dict = api.dict();
    const user = stores.user();

    const emit = defineEmits(['update', 'edit']);
    const { t } = useI18n();

    const props = defineProps({
        item: { type: [Object, null], default: null }
    });

    const data = reactive({
        instruments: [],
        moods: [],
        categories: [],
        genres: [],
        tags: [],

        id: null,
        loading: true
    });

    const canEdit = computed(() => {
        if (data.id) {
            return api.artists().canEdit({
                artist: { id: data.id }
            });
        }

        return api.artists().canAdd();
    });

    const onInstrumentsChange = ({ values }) => {
        data.instruments = values?.length ? values : [];

        onEdit();
    }

    const onMoodsChange = ({ values }) => {
        data.moods = values?.length ? values : [];

        onEdit();
    }

    const onCategoriesChange = ({ values }) => {
        data.categories = values?.length ? values : [];

        onEdit();
    }

    const onGenresChange = ({ values }) => {
        data.genres = values?.length ? values : [];

        onEdit();
    }

    const onTagsChange = ({ values }) => {
        data.tags = values?.length ? values : [];

        onEdit();
    }

    const onEdit = () => {
        emit('edit');
        data.hasChanges = true;
    }

    const saveChanges = async () => {
        if (!data.id) {
            return;
        }

        const artistInfo = {
            dicts: [
                ...data.instruments.map(item => item.id),
                ...data.moods.map(item => item.id),
                ...data.categories.map(item => item.id),
                ...data.genres.map(item => item.id),
                ...data.tags.map(item => item.id)
            ]
        }

        data.updating = true;

        const payload = await artists.update(data.id, artistInfo);

        await delay(200);
        data.updating = false;
        delete data.hasChanges;

        message.info(t(`pages.artists.edit.done.title`));

        emit('update', payload);
    }

    const getItem = async () => {
        data.loading = true;

        const types = {};

        (await dict.getTypes())?.forEach(item => types[item.code] = item.id);

        const payload = await artists.get(props.item.id);

        Object.keys(types).forEach(type => {
            if (data[type]) {
                data[type] = payload?.dicts.filter(item => item.dictId === types[type]).map(item => ({ ...item, name: item.title })) || [];
            }
        });

        data.id = payload?.id;

        await delay(200);

        data.loading = false;
        delete data.hasChanges;
    }

    onMounted(() => {
        if (props.item?.id) {
            getItem();
        } else {
            data.loading = false;
        }
    });
</script>

<style lang="scss" scoped>
    .loader {
        @include inside-loader;
    }

    .artist-metadata {
        display: flex;
        flex-direction: column;
        gap: 24px;
        .title {
            font: var(--font-h3);
            font-weight: bold;
            color: var(--dark-500);
        }
    }
</style>
